import React , {Component} from 'react';
import { NavbarBrand } from 'react-bootstrap';
import {Navbar , Jumbotron , Nav , NavbarToggler ,Modal , Button , ModalHeader , ModalBody, Collapse, NavItem, FormGroup , Form , Input , Label } from 'reactstrap';

import {NavLink} from 'react-router-dom';
class Header extends Component {
constructor(props){
    super(props);
    this.state= {
isNavOpen:false , 
isModalOpen: false
    };
   
    this.toogleNav = this.toggleNav.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
    this.handleLogin = this.handleLogin.bind(this);
}
toggleNav( ) {
    this.setState({
        isNavOpen: !this.state.isNavOpen
    })
}
toggleModal( ) {
    this.setState({
        isModalOpen: !this.state.isModalOpen
    })
} 
handleLogin( ) {
    this.toggleModal();
    alert('Username:'+ this.username . value +"Password:" + this.password.value + 'Remember:' +this.remember.checked);
    //.preventDefault();
   // event.preventDefault();
}

render() {
    return (
        <>
          <Navbar dark expand='md'>
     
       <div className="container">
           <NavbarToggler onClick={this.toogleNav} />
         <NavbarBrand className='mr-auto' herf="/">
           <img src="assets/images/logo.png" height ="30 " width="41" alt = "Restorante Con Fusion" />
         </NavbarBrand>
         <Collapse isOpen ={this.state.isNavOpen} navbar>
         <Nav navbar >
           
         <NavItem>
                                <NavLink className="nav-link"  to='/home'><span className="fa fa-home fa-lg"></span> Home</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to='/aboutus'><span className="fa fa-info fa-lg"></span> About Us</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link"  to='/menu'><span className="fa fa-list fa-lg"></span> Menu</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to='/contactus'><span className="fa fa-address-card fa-lg"></span> Contact Us</NavLink>
                            </NavItem>
         </Nav>
         <Nav className="ml-auto" navbar>
             <NavItem>
<Button outline onClick={this.toggleModal}>
    <span className="fa fa-sign-in fa-lg"></span>
    Login
</Button>
             </NavItem>
             </Nav>
         </Collapse>
         </div>
       </Navbar>
       <Jumbotron>
       <div className="container">

           <div className="row row-header">
               <div className="col-12 col-sm-6">
                   <h1>
                       Restorant Con fusion
                   </h1>
                   <p>
                       We take inspiration from the world best cusines , and create a unique fusion of experience . Our lipsmacking creations will tickle your  culiration. 
                   </p>
               </div>
           </div>
           </div>
       </Jumbotron>
       <Modal isOpen = {this.state.isModalOpen} toggle={this.toggleModal}>
           <ModalHeader toggle={this.toggleModal}>
               Login
           </ModalHeader>
           <ModalBody>
<Form onSubmit={this.handleLogin}>
    <FormGroup>
        <Label htmlFor='username'>Username</Label>
        <Input type='text ' id="username" name="username"
        innerRef={(input) => this.username= input} />
    
    </FormGroup>
   
    <FormGroup>
                                <Label htmlFor="password">Password</Label>
                                <Input type="password" id="password" name="password"
                                    innerRef={(input) => this.password = input}  />
                            </FormGroup>
    <FormGroup check>
<Label check>
    <Input type='checkbox' name='remember'
      innerRef={(input) => this.remember= input} />
     Remember me
</Label>
    </FormGroup>
    <Button type='submit' value = 'submit' color="primary ">Login</Button>
</Form>
           </ModalBody>
       </Modal>
        </>
    );
}

}
export default Header;